"use client";

import { marked } from "marked";
import Container from "./components/container";
import jobsApi from "./lib/jobs-api";
import { useEffect } from "react";

export default function Home() {
  useEffect(() => {
    fetch(jobsApi())
      .then(response => response.text())
      .then(markdown => {
        // Convert the Markdown text to HTML using the marked library.
        const htmlContent = marked.parse(markdown);
        // Insert the HTML into the page.
        console.log(htmlContent);
      })
      .catch(error => console.error('Error fetching Markdown:', error));
  }, []);

  return (
    <div className="">
      <Container>
        <div className="flex items-center justify-center p-4">
          <div className="relative">
            <input
              type="text"
              placeholder="Search..."
              className="w-80 px-4 py-2 border border-gray-300 rounded-full focus:outline-none focus:ring-2 focus:ring-blue-500 transition"
            />
            <button className="absolute inset-y-0 right-0 flex items-center pr-3">
              <svg
                className="h-5 w-5 text-gray-500"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M21 21l-4.35-4.35M9 17a8 8 0 100-16 8 8 0 000 16z"
                />
              </svg>
            </button>
          </div>
        </div>
      </Container>
    </div>
  );
}
